* {
    box-sizing: border-box;
}

body {
    margin: 0;
    height: 100vh;
    width: 100vw;
    background-color: black;
    color: #ffffff;
    font-family: sans-serif;
}

.display-block {
    display: block !important;
}

.display-none {
    display: none !important;
}

.jg-main {
    height: 100vh;
    width: 100vw;
}

.selection {
    position: relative;
    z-index: 1;
    width: 50%;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.selection-item:not(:first-child) {
    margin-top: 10px;
}

.selection-item {
    cursor: pointer;
    width: 200px;
    padding: 10px;
    text-align: center;
    border: 1px solid;
    color: #ffffff;
    text-decoration: none;
}

#about-me-video {
    opacity: 0;
    -webkit-filter: grayscale(100%);
    -moz-filter: grayscale(100%);
    -ms-filter: grayscale(100%);
    -o-filter: grayscale(100%);
    filter: grayscale(100%);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    width: auto;
    max-width: 100%;
    transition: opacity 0.5s ease;
}

#about-me-video.active {
    opacity: 1;
}